import React, { useState } from "react"
import styled from "@emotion/styled"
import useToggle from "./useToggle"

/**
 * CSS
 */
const Container = styled.div`
  display: flex;
	width: auto;
	align-items: flex-end;
  flex-direction: column;
	@media (max-width: 768px) {
		align-items: center;
		align-content: center;
		padding: 50px 0px 0px 0px;
  }
  padding: 50px 50px;
	align-content: flex-end;

  .button {
		font-size: 14px;
		background-color: white;
    display: inline-block;
    line-height: 25px;
    height: 25px;
    text-align: center;
    width: 25px;
    outline: 1px solid var(--color-primary);
  }

  .button:hover {
    background-color: var(--color-primary);
    color: white;
    border: none;
    cursor: pointer;
  }

  .background-active {
    background-color: var(--color-primary);
    color: white;
  }
`
const Text = styled.p`
	font-size: 14px;
	box-sizing: border-box;
  margin-top: 1px;
  width: 300px;
	@media (max-width: 768px) {
		width: 62.5vw;
  }
  display: inline-block;
  /* line-height: var(--lineHeight-normal); */
  line-height: 1.375em;
  padding: 12.5px;
  outline: 1px solid var(--color-primary);
	background-color: white;
`

/**
 * HTML
 */
const Info = props => {
  const [isOn, toggleIsOn] = useToggle();

  return (
    <Container>
      <div
        onClick={toggleIsOn}
				className={`button sansFont ${isOn ? "background-active" : ""}`}
      >
        {props.text}
      </div>
      {isOn && (
        <Text className="primaryColor sansFont">
          Marcaz  is an interdisciplinary space for sharing research,
          critiques, histories, and new narratives around visual culture.
        </Text>
      )}
    </Container>
  )
}

export default Info
